import './container.scss';
import { useRef, useEffect, useState } from 'react';
import { useSpring, animated } from 'react-spring';
import { Map } from '../map/Map';
import { Header } from '../stateless/header/Header';
import { LanguagePicker } from '../form/languagepicker/LanguagePicker';
import { Footer } from '../stateless/footer/Footer';
import { TravelDuration } from '../form/TravelDuration';
import { DestinationDetails } from '../destinationDetails/DestinationDetails';
import { VelorouteDetails } from '../velorouteDetails/VelorouteDetails';
import { useDispatch, useSelector } from 'react-redux';
import { 
    selectActiveSection,
    setActiveSection,
    setCurrentTrainroutes
} from '../map/trainroutes/TrainroutesSlice';
import { 
    selectActiveVeloroute,
    setActiveVeloroute,
    setActiveVelorouteSection 
} from '../map/veloroutes/VeloroutesSlice';
import {
    setActiveDestination,
    selectActiveDestinationId
} from '../destinationDetails/DestinationDetailsSlice';
export const Container = ({setLanguage, lang}) => {

    const dispatch = useDispatch();
    const activeDestination = useSelector(selectActiveDestinationId);
    const activeSection = useSelector(selectActiveSection);
    const activeVeloroute = useSelector(selectActiveVeloroute);

    const [value, setValue] = useState(0);
    const [submitVal, setSubmitVal] = useState(0);
    const [dimensions, setDimensions] = useState([0, 0]);
    const [containerHeight, setContainerHeight] = useState(0);
    const [destDetailsActive, setDestDetailsActive] = useState(false);
    const veloDetailsActive = false; // refactor

    const container = useRef(null);
    const wrapper = useRef(null);
    const prevValue = useRef(0);
    const direction = useRef(1);

    const asideWidth = activeVeloroute ? '100vw' : '130vw';

    const containerStyles = useSpring({
        width: (activeDestination || activeSection) ? asideWidth : '160vw',
        onRest: () => { setDestDetailsActive(prev => !prev) }
    });

    const handleInputChange = ({target}) => {
        const val = target.value;
        setValue(val);
    }
    
    const handleSubmit = (e) => {
        e.preventDefault();
        prevValue.current = value;
        setSubmitVal(value);
        dispatch(setCurrentTrainroutes(value));
        dispatch(setActiveDestination(null));
        dispatch(setActiveSection(null));
        dispatch(setActiveVeloroute(null));
        dispatch(setActiveVelorouteSection(null));
    }

    useEffect(()=>{
        let dir = direction.current;
        if(value < prevValue.current) dir = -1;
        if(value > prevValue.current) dir = 1;
        direction.current = dir;
    },[value]);

    useEffect(()=> {
        if(!wrapper) return

        const setSize = () => {
            const wrapperWidth = wrapper.current.getBoundingClientRect().width;
            const wrapperHeight = wrapper.current.getBoundingClientRect().height;
            const ratio = wrapperWidth / 16 * 9 > wrapperHeight ? 'horizontal' : 'vertical';
            const height = ratio==='horizontal' ? wrapperWidth / 16 * 9 : wrapperHeight;
            const width = ratio==='horizontal' ? wrapperWidth : wrapperHeight / 9 * 16;

            setDimensions([
                width,
                height
            ])
        }

        setSize();
        window.addEventListener('resize', setSize);
        return () => {
            window.removeEventListener('resize', setSize);
        }
        
    },[wrapper.current?.offsetWidth]);

    useEffect(() => {
        if(!container) return
        
        const setHeight = () => { 
            const h = container.current.getBoundingClientRect().height;
            setContainerHeight(h) 
        }
        setHeight();
        window.addEventListener('resize', setHeight);
        return () => {
            window.removeEventListener('resize', setHeight);
        }
    }, []);

    return (<div id="wrapper">
        <Header>
            <div id="logo"/>
          <LanguagePicker
              setLanguage={setLanguage}
              lang={lang}/>
        </Header>
        <animated.div 
            id="container" 
            ref={container} 
            style={{...containerStyles, height: containerHeight}}
            >
            <aside 
                className="destination-details-container">
                <DestinationDetails
                    parent={container.current}
                    detailsActive={destDetailsActive}
                    lang={lang}/>
                <VelorouteDetails
                    parent={container.current}
                    detailsActive={veloDetailsActive}
                    lang={lang}/>
            </aside>
            {/*<animated.aside 
                className="veloroute-details-container"
                style={velorouteStyles}>
                
            </animated.aside>*/}
            <main>
                <div id="map-wrapper" ref={wrapper}>
                    
                    {/*<div className="debug" style={{ position: 'absolute', left: '45%', top: '50%', width: '10%', borderBottom: '1px dashed blue', zIndex: 99}}/>
                    <div className="debug" style={{ position: 'absolute', top: '45%', left: '50%', height: '10%', borderRight: '1px dashed blue', zIndex: 99}}/>*/}
                    
                    <Map 
                        value={submitVal}
                        wrapper={wrapper.current}
                        dimensions={dimensions}
                        />
                </div>
            </main>
        </animated.div>
        <Footer>
            <TravelDuration
                handleSubmit={handleSubmit}
                rangeValue={value}
                handleInputChange={handleInputChange}
                lang={lang}/>
        </Footer>
    </div>)
}