import './rangeinput.scss';
import { useRef, useEffect } from 'react';

export const RangeInput = ({
    id,
    min,
    max,
    value,
    step,
    handleInputChange
}) => {

    const ref = useRef(null);

    useEffect(() => {
        const trackLength = (value - min) * 100 / (max - min) + '% 100%';
        ref.current.style.backgroundSize = trackLength;
    },[value, min, max]);

    const skala = () => {
        const rangeSkala = [];
        for (let i = 0; i <= max; i++){
            const hour = Math.floor(i / 2);
            const minute = i % 2 === 1 ? '30' : '00';
            const time = i < parseFloat(max) ? `${hour}:${minute}` : `${hour}+`;
            rangeSkala.push(<li key={i}>
                    <span>{time}</span>
                </li>)
        }
        return rangeSkala
    }

    return (<div className="range-slider">
        <input 
            ref={ref}
            type="range" 
            id={id} 
            name={id} 
            min={min} 
            max={max}
            value={value} 
            step={step}
            onChange={handleInputChange}/>
            <div className="rangeInputWrapper">
                <ul 
                    className="steps"
                    style={{
                        width: `${100 + 100 / (parseFloat(max))}%`,
                    }}>
                    {skala()}
                </ul>
            </div>
        </div>)
}