import './App.scss';
import { Container } from './components/container/Container';
import { useEffect, useState } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { loadTrainroutes, selectStartPos } from './components/map/trainroutes/TrainroutesSlice';
import { loadLang, langLoading } from './AppSlice';

function App() {

  const [lang, setLang] = useState('de');
  const dispatch = useDispatch();
  const languagesLoading = useSelector(langLoading);
  const start = useSelector(selectStartPos);

  const setLanguage = lang => {
    setLang(lang)
  }

  useEffect(()=>{
    dispatch(loadTrainroutes({start}));
  },[dispatch, start]);

  useEffect(()=>{
    dispatch(loadLang());
  },[dispatch]);
  
  if(languagesLoading) return <div>'languages loading'</div>;
  return (
    <div className="App">
      <Container lang={lang} setLanguage={setLanguage}/>
    </div>);
}

export default App;
